<template>
  <div>
    <van-tabs
      v-model="active"
      title-active-color="#53BAC1"
      color="#53BAC1"
      sticky
    >
      <van-tab title="项目详情">
        <div class="pageMain" v-if="projectInfo">
          <van-cell-group class="contentBox">
            <van-cell title="劳资专管员" :value="projectInfo.transactor?projectInfo.transactor:'-'" />
            <van-cell title="手机号码" :value="projectInfo.transactorTel?projectInfo.transactorTel:'-'" />
            <van-cell title="项目经理姓名" :value="projectInfo.projectManager?projectInfo.projectManager:'-'" />
            <van-cell title="手机号码" :value="projectInfo.tel?projectInfo.tel:'-'" />
          </van-cell-group>
          <van-cell-group class="contentBox">
            <van-cell title="项目名称" :value="projectInfo.projectName?projectInfo.projectName:'-'" />
            <van-cell title="所属行业" :value="projectInfo.departName?projectInfo.departName:'-'" />
            <van-cell title="工程造价" :value="projectInfo.total?projectInfo.total:'-'" />
            <van-cell title="开工日期" :value="projectInfo.startDate?projectInfo.startDate:'-'" />
            <van-cell title="建设周期" :value="(projectInfo.timeLimit?projectInfo.timeLimit:'-')+'天'" />
            <van-cell title="项目编号" :value="projectInfo.projectCode?projectInfo.projectCode:'-'" />
            <van-cell title="所属区域" :value="projectInfo.areaName?projectInfo.areaName:'-'" />
            <van-cell title="项目地址" :value="projectInfo.address?projectInfo.address:'-'" />
            <van-cell title="建设方" :value="projectInfo.owerCompanyName" is-link :to="'/buildPage/'+projectInfo.owerCompany" />
            <van-cell title="施工方" :value="projectInfo.companyName" is-link :to="'/buildPage/'+projectInfo.companyId" />
            <van-cell title="工资代发" :value="projectInfo.dfBank?projectInfo.dfBank:'-'" />
            <van-cell title="工资发放日" :value="'每月'+(projectInfo.sendDate?projectInfo.sendDate:'-')+'日'" />
            <van-cell title="项目状态">
              <template>
                <span v-if="projectInfo.state == '01'">
                  未开工
                </span>
                <span v-else-if="projectInfo.state == '02'">
                  已开工
                </span>
                <span v-else-if="projectInfo.state == '03'">
                  已停工
                </span>
                <span v-else-if="projectInfo.state == '04'">
                  已竣工
                </span>
                
              </template>
            </van-cell>
          </van-cell-group>
        </div>
        <div v-else>
            <van-empty description="暂无项目详情" />
          </div>
      </van-tab>
      <van-tab title="劳动协议">
        <div class="pageMain">
          <van-cell-group class="contentBox" v-if="contractInfo">
            <van-cell title="甲方(用人单位)" :value="contractInfo.companyName?contractInfo.companyName:'-'" />
            <van-cell title="统一社会信用代码" :value="contractInfo.companyCode?contractInfo.companyCode:'-'" />
            <van-cell title="用人单位地址" :value="contractInfo.companyAddress?contractInfo.companyAddress:'-'" />
            <van-cell title="乙方(劳动者)" :value="contractInfo.employeeName?contractInfo.employeeName:'-'" />
            <van-cell title="出生年月" :value="contractInfo.employeeBirthday?contractInfo.employeeBirthday:'-'" />
            <van-cell title="身份证号码" :value="contractInfo.employeeIdCard?contractInfo.employeeIdCard:'-'" />
            <van-cell title="户口所在地" :value="contractInfo.employeeProvince||contractInfo.employeeCity?contractInfo.employeeProvince+contractInfo.employeeCity:'-'" />
            <van-cell title="劳动协议期限" :value="contractInfo.effectiveStartDate+'-'+contractInfo.effectiveEndDate" />
            <van-cell title="签订项目" :value="contractInfo.projectName?contractInfo.projectName:'-'" />
            <van-cell title="项目地址" :value="contractInfo.projectAddres?contractInfo.projectAddres:'-'" />
            <van-cell title="岗位" :value="contractInfo.workClass?contractInfo.workClass:'-'" />
            <van-cell title="班组" :value="contractInfo.groupName?contractInfo.groupName:'-'" />
            <van-cell title="工作地" :value="contractInfo.projectAddres?contractInfo.projectAddres:'-'" />
            <van-cell title="工时" value="标准工时" />
            <van-cell title="工资计量单位" :value="contractInfo.unit?contractInfo.unit:'-'" />
            <van-cell title="工资计量单价" :value="contractInfo.price?contractInfo.price:'-'" />
            <van-cell title="工资发放日" :value="'每月'+(contractInfo.sendDate?contractInfo.sendDate:'-')+'号'" />
          </van-cell-group>
          <div v-else>
            <van-empty description="暂无劳动协议详情" />
          </div>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
export default {
  name: "Project",
  data() {
    return {
      active: 0,
      projectInfo: null,
      contractInfo: null
    };
  },
  created () {
    let projectId = JSON.parse(localStorage.getItem("project")).projectid
    //项目详情
    this.getProjectList(projectId);
    //劳动协议
    this.getTContractInfoById(projectId)
  },
  methods: {
    getProjectList(projectId) {
      this.$http.jsb.getProjectInfoByProjectId({projectId}).then(res=>{
        if(res.code == 200){
          this.projectInfo = res.data;
        }
      })
    },
    getTContractInfoById(projectId){
      this.$http.jsb.getTContractInfoById({projectId}).then(res=>{
        if(res.code == 200){
          this.contractInfo = res.data
        }else{

        }
      })
    }
  },

};
</script>

